import React, { useState, useEffect } from "react";
import mammoth from "mammoth";

function shuffleArray(array) {
  const arr = [...array];
  for (let i = arr.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [arr[i], arr[j]] = [arr[j], arr[i]];
  }
  return arr;
}

function parseQuestions(rawText) {
  const questions = [];
  const blocks = rawText.split(/\n(?=\d+\))/);

  for (const block of blocks) {
    const lines = block.trim().split("\n");
    if (lines.length < 5) continue;

    const questionLine = lines[0];
    const options = lines.slice(1, 5);
    const answerLine = lines.find((line) =>
      line.toLowerCase().includes("đáp án")
    );

    if (!answerLine) continue;

    const correctLetter = answerLine
      .slice(answerLine.indexOf(":") + 1)
      .trim()
      .toUpperCase();

    const correctOption = options.find(
      (opt) => opt.trim().toUpperCase().startsWith(correctLetter)
    );

    if (!correctOption) continue;

    questions.push({
      question: questionLine,
      options: shuffleArray(options),
      answer: correctOption,
      userAnswer: "",
    });
  }

  return questions;
}

export default function QuizGenerator() {
  const [inputText, setInputText] = useState("");
  const [quiz, setQuiz] = useState([]);
  const [submitted, setSubmitted] = useState(false);
  const [timeLeft, setTimeLeft] = useState(null);

  const handleGenerate = () => {
    const parsed = parseQuestions(inputText);
    setQuiz(parsed);
    setSubmitted(false);
    setTimeLeft(parsed.length * 60);
  };

  const handleSelect = (qIdx, optionText) => {
    const updatedQuiz = [...quiz];
    updatedQuiz[qIdx].userAnswer = optionText;
    setQuiz(updatedQuiz);
  };

  const score = quiz.filter((q) => q.answer === q.userAnswer).length;

  useEffect(() => {
    if (timeLeft === null || submitted) return;
    if (timeLeft <= 0) {
      setSubmitted(true);
      return;
    }
    const timer = setInterval(() => {
      setTimeLeft((prev) => prev - 1);
    }, 1000);
    return () => clearInterval(timer);
  }, [timeLeft, submitted]);

  const formatTime = (t) => {
    const m = Math.floor(t / 60);
    const s = t % 60;
    return `${m}:${s.toString().padStart(2, "0")}`;
  };

  const handleFileUpload = async (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const arrayBuffer = await file.arrayBuffer();
    const result = await mammoth.extractRawText({ arrayBuffer });
    setInputText(result.value);
  };

  const progress = quiz.length > 0 ? Math.floor((quiz.filter(q => q.userAnswer).length / quiz.length) * 100) : 0;

  return (
    <div className="min-h-screen bg-[url('https://i.imgur.com/VIaK3Iq.png')] bg-repeat bg-center text-gray-900 p-4 font-serif">
      <div className="max-w-4xl mx-auto bg-white bg-opacity-90 rounded-lg shadow-lg p-6 space-y-6">
        <h1 className="text-4xl font-bold text-center border-b pb-2">📝 ĐỀ TRẮC NGHIỆM</h1>

        <textarea
          className="w-full h-48 p-3 border border-gray-300 rounded bg-gray-50 shadow-sm"
          placeholder="Dán văn bản câu hỏi vào đây"
          value={inputText}
          onChange={(e) => setInputText(e.target.value)}
        ></textarea>

        <input type="file" accept=".docx" onChange={handleFileUpload} className="mt-2" />

        <button
          onClick={handleGenerate}
          className="px-6 py-3 bg-blue-700 hover:bg-blue-800 text-white font-semibold rounded shadow w-full"
        >
          🚀 Tạo đề trắc nghiệm
        </button>

        {quiz.length > 0 && (
          <div className="space-y-6">
            {!submitted && timeLeft !== null && (
              <div className="text-center text-lg font-semibold border p-2 bg-yellow-100 rounded">
                ⏱ Thời gian còn lại: {formatTime(timeLeft)}
              </div>
            )}

            <div className="w-full h-4 bg-gray-200 rounded-full overflow-hidden">
              <div className="h-full bg-green-500 transition-all duration-500" style={{ width: `${progress}%` }}></div>
            </div>

            {quiz.map((q, idx) => (
              <div key={idx} className="border-b pb-4">
                <p className="font-semibold mb-2">Câu {idx + 1}: {q.question}</p>
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
      {q.options.map((opt, i) => {
  const isSelected = q.userAnswer === opt;

  return (
    <button
      key={i}
      onClick={() => handleSelect(idx, opt)}
      className={`w-full text-left p-5 rounded-xl border-4 transition-all duration-150
        ${
          isSelected
            ? "bg-blue-700 text-white font-extrabold text-lg border-blue-900 shadow-lg scale-[1.03]"
            : "bg-white hover:bg-blue-100 text-gray-900 font-medium text-base border-gray-300"
        }
      `}
    >
      {opt}
    </button>
  );
})}
                </div>
                {submitted && (
                  <p className="mt-2 text-sm">
                    Đáp án đúng: <b className="text-green-700">{q.answer}</b>
                  </p>
                )}
              </div>
            ))}

            {!submitted && (
              <button
                onClick={() => setSubmitted(true)}
                className="w-full py-3 bg-green-600 hover:bg-green-700 text-white rounded font-semibold shadow"
              >
                ✅ Nộp bài
              </button>
            )}

            {submitted && (
              <div className="text-xl font-bold text-center text-blue-700 mt-4">
                🎉 Bạn đúng {score}/{quiz.length} câu
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
